<template>
  <a-page-header
    style="border: 1px solid rgb(235, 237, 240)"
    title="出行助手"
    sub-title="！占位用"
  >
    <template slot="extra">
      <a-auto-complete
        placeholder="input search text"
        :data-source="datas"
        v-model="value"
        allow-clear
      >
      </a-auto-complete>
    </template>
  </a-page-header>
</template>

<script>
import Locations from "../../data/outting/locating.js";

export default {
  model: { prop: "area", event: "change" },
  data() {
    return {
      value: this.area,
      datas: Object.keys(Locations),
    };
  },
  watch: {
    value(v) {
      this.$emit("change", v || "");
    },
  },
};
</script>